// __tests__/Header.test.js
import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import Header from '@/components/layout/Header';

describe('Header', () => {
  it('affiche le titre', () => {
    const { getByText } = render(<Header title="Mon titre" />);
    expect(getByText('Mon titre')).toBeTruthy();
  });

  it('appelle onBackPress quand on clique sur le bouton retour', () => {
    const mockBack = jest.fn();
    const { getByRole } = render(<Header title="Retour" onBackPress={mockBack} />);
    const backButton = getByRole('button');
    fireEvent.press(backButton);
    expect(mockBack).toHaveBeenCalled();
  });

  it('affiche l’icône à droite et appelle onRightPress', () => {
    const mockRight = jest.fn();
    const { getByRole } = render(
      <Header title="Test" rightIcon="settings" onRightPress={mockRight} />
    );
    const rightButton = getByRole('button', { name: '' }); // Il y a 2 boutons donc le premier est le back
    const allButtons = render(
      <Header title="Test" rightIcon="settings" onRightPress={mockRight} />
    ).UNSAFE_getAllByRole('button');
    fireEvent.press(allButtons[1]); // clic sur le bouton de droite
    expect(mockRight).toHaveBeenCalled();
  });
});
